header{
	height: 100vh;
	padding-top: 4rem;
	overflow: hidden;
}

.header__container{
  text-align: center;
  height: 100%;
  position: relative;
}


.cta {
  margin-top: 2.5rem;
  display: flex;
  justify-content: center;
  
}

.header__socials{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content:'';
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 2rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 6rem 2.5rem 1.5rem 2rem;
}


.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 200;
  font-size: 0.9rem;
  
}

@media screen and (max-width: 1024px){
  header {
    height: 68vh;
  }
}

@media screen and (max-width: 600px){
  header{
    height: 100vh;
  }
  .header__socials,.scroll__down{
    display: none;
  }
}